<template>
  <div ref="chartContainer" :style="{ width: '100%', height: '400px' }"></div>
</template>

<script>
import * as echarts from 'echarts';
import * as XLSX from 'xlsx';

export default {
  name: 'LineChart',
  props: {
    chartDataUrl: {
      type: String,
      required: true,
    },
    sheetName: {
      type: String,
      required: true,
    },
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        const response = await fetch(this.chartDataUrl);
        const arrayBuffer = await response.arrayBuffer();
        const data = new Uint8Array(arrayBuffer);
        const workbook = XLSX.read(data, { type: 'array' });
        const sheet = workbook.Sheets[this.sheetName];
        const jsonData = XLSX.utils.sheet_to_json(sheet);

        const categories = jsonData.map(item => item.date);


        const series = [
          {
            name: '预测值',
            type: 'line',
            data: jsonData.map(item => item.test_value),
            itemStyle: { color: 'green' },
          },
        ];

        this.initChart(categories, series);
      } catch (error) {
        console.error('Error fetching data:', error.message);
      }
    },
    initChart(categories, series) {
      const chart = echarts.init(this.$refs.chartContainer);
      const options = {
        tooltip: {
          trigger: 'axis',
          formatter: function(params) {
            const xAxisValue = params[0].axisValue;
            const seriesData = params.map(
              (param) => `${param.seriesName}: ${param.value}`
            );
            return `天数: ${xAxisValue}<br>${seriesData.join('<br>')}`;
          },
        },
        xAxis: {
          type: 'category',
          data: categories,
        },

        yAxis: {
          type: 'value',
          name: this.sheetName,
          nameLocation: 'middle',
          nameGap: 60,
          nameTextStyle: {
            fontSize: 14,
            fontWeight: 'bold',
            padding: [0, 0, 0, 120],
          },
          axisLabel: {
            formatter: function(value) {
              return value.toExponential(2);
            },
          },
        },
        series: series,
      };
      chart.setOption(options);
    },
  },
};
</script>

