<template>
  <div>
    <div ref="chartContainer" style="width: 100%; height: 400px;"></div>
    <div ref="sliderContainer" style="width: 100%; height: 50px;"></div>
  </div>
</template>

<script>
import * as echarts from 'echarts';
import * as XLSX from 'xlsx';

export default {
  name: 'LineChart_2',
  props: {
    chartDataUrl: {
      type: String,
      required: true,
    },
  },
  mounted() {
    this.fetchData();
  },
  methods: {
    async fetchData() {
      try {
        const workbook = await this.readExcelFile(this.chartDataUrl);
        const worksheet = workbook.Sheets[workbook.SheetNames[0]];
        const jsonData = XLSX.utils.sheet_to_json(worksheet, { header: 1 });

        // 提取数据
        const categories = jsonData.slice(1).map(row => row[0]);
        const future = jsonData.slice(1).map(row => row[1]);
        const up = jsonData.slice(1).map(row => row[2]);
        const low = jsonData.slice(1).map(row => row[3]);

        // 格式化数据
        const seriesData = [
          { name: '预测上界', data: up },
          { name: '预测值', data: future },
          { name: '预测下界', data: low },
        ];

        // 初始化折线图
        this.initChart(categories, seriesData);

        // 初始化滑动条
        this.initSlider(categories);
      } catch (error) {
        console.error('Error fetching data:', error.message);
      }
    },
    async readExcelFile(url) {
      const response = await fetch(url);
      const arrayBuffer = await response.arrayBuffer();
      const data = new Uint8Array(arrayBuffer);
      const workbook = XLSX.read(data, { type: 'array' });
      return workbook;
    },
    initChart(categories, seriesData) {
      const chart = echarts.init(this.$refs.chartContainer);
      const options = {
        tooltip: {
          trigger: 'axis',
        },
        xAxis: {
          type: 'category',
          data: categories,
        },
        yAxis: {
          type: 'value',
        },
        series: seriesData.map(item => ({
          name: item.name,
          type: 'line',
          data: item.data,
          itemStyle: {
            color: params => {
              const futureData = seriesData[1].data;
              const upperBoundData = seriesData[0].data;
              const lowerBoundData = seriesData[2].data;

              // 判断当前数据点是否超过预测上界或小于预测下界
              if (futureData[params.dataIndex] > upperBoundData[params.dataIndex] ||
                  futureData[params.dataIndex] < lowerBoundData[params.dataIndex]) {
                return 'red'; // 超过预测上界或小于预测下界的数据点使用红色
              } else {
                return 'green'; // 其他数据点使用默认颜色
              }
            },
          },
        })),
      };
      chart
      chart.setOption(options);
    },
    initSlider(categories) {
      const sliderChart = echarts.init(this.$refs.sliderContainer);
      const sliderOptions = {
        xAxis: {
          type: 'category',
          data: categories,
        },
        yAxis: {
          show: false,
        },
        dataZoom: [
          {
            type: 'slider',
            start: 0,
            end: Math.min(100 * 5 / categories.length, 100),
            filterMode: 'filter',
          },
        ],
      };
      sliderChart.setOption(sliderOptions);

      // 将滑动事件绑定到折线图上
      const mainChart = echarts.getInstanceByDom(this.$refs.chartContainer);
      sliderChart.on('dataZoom', ({ start, end }) => {
        mainChart.setOption({
          dataZoom: [
            {
              start,
              end,
            },
          ],
        });
      });
    },
  },
};
</script>
